* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.secondPage {
    position: relative;
    height: 100vh;
    width: 100%;
    background-color: black;

    .secondPageText {
        width: 80%;
        color: white;
        font-size: 0.8rem;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
        font-family: 'Noto Sans', sans-serif;
        

        font-family: 'Quicksand', sans-serif;
        font-weight: 400;
    }

    .arrowDownTwo {
        height: 26px;
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, -50%);
    }
}