* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

$main-color: #69510F;


.image {
    position: relative;
    height: 100vh;
    max-width: 100%;
    background-image: url(../svg/threee.jpg);
    background-size: cover;
    background-position: center;

    .menuContainer {
        height: 100vh;
        width: 20%;
        position: absolute;
        display: grid;
        grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
        grid-template-columns: 1fr;
        text-align: center;
        align-items: center;
        background-color: whitesmoke;
    
    
        .menuElement {
            color: black;
            transform: rotate(270deg);
            font-size: 0.75rem;
            font-family: 'PT Sans Caption', sans-serif;
            cursor: pointer;
        }
    }

    .titleBox {
        position: absolute;
        bottom: 15%;
        left: 60%;
        transform: translate(-50%, -50%);
        text-align: center;

        .title {
            color: white;
            font-size: 1.6rem;
            font-family: 'Bitter', serif;
            font-weight: 400;
        }
    
        .subtitle {
            color: $main-color;
            font-family: 'Bitter', serif;
            font-size: 1rem;
            font-weight: 400;

            display: flex;
            width: 100%;
            justify-content: center;
            align-items: center;
            text-align: center;
        }

        
        .subtitle:after, .subtitle:before {
            // content: '';
            // display: block;
            // position: absolute;
            // align-items: center;
            // background-color: $main-color;
            // width: 50px;
            // height: 3px;

            content: '';
            border-top: 1px solid;
            margin: 0 5% 0 5%;
            width: 20px;
        }
    }

    .arrowDown {
        height: 26px;
        position: absolute;
        left: 60%;
        bottom: 0;
        transform: translate(-50%, -50%);
    }
}