* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

$main-color: #69510F;

.sixPage {
    height: 100vh;
    width: 100%;
    background-color: black;

    .menuTitleContainer {
        position: relative;
        height: 10vh;
        width: 100%;

        .menuSubtitle {
            color: $main-color;
            text-align: center;
            font-family: 'Rokkitt', serif;
            font-weight: 400;
            font-size: 0.9rem;

            position: absolute;
            top: 40%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .menuTitle {
            color: whitesmoke;
            text-align: center;
            font-family: 'Rokkitt', serif;
            font-weight: 400;
            font-size: 1.2rem;

            position: absolute;
            top: 65%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .menuContainer {
        height: 80vh;
        width: 100%;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
        font-family: 'Rokkitt', serif;
        font-weight: 400;

        .menuGrid {
            position: relative;
            padding-left: 10%;
            padding-right: 10%;

            .dishContainer {
                transform: translateY(50%);


                .dishName {
                    color: $main-color;
                    font-size: 0.8rem;
                    display: flex;
                    align-items: center;
                    font-weight: 400;
                }
    
                .dishName::after {
                    content: '';
                    border-top: 1px solid $main-color;
                    margin-left: 5%;
                    width: 35%;
                }
    
                .menuIngredients {
                    font-family: 'Rokkitt', serif;
                    color: whitesmoke;
                    font-size: 0.6rem;
                    font-weight: 400;
                }
    
                .dishPrice {
                    color: $main-color;
                    font-size: 0.8rem;
                    font-family: 'Rokkitt', serif;
                    font-weight: 400;
                }
            }
        }
    }

    .menuArrowDown {
        position: relative;
        height: 10vh;
        width: 100%;

        .arrowDownFive {
            height: 26px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
}