* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

$main-color: #69510F;

.seventhPage {
    height: 100vh;
    width: 100%;
    background-color: black;

    .starterContainer {
        position: relative;
        height: 30%;
        width: 100%;

        .starter {
            height: 100%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .starterTitle {
            color: $main-color;
            font-size: 1rem;
            font-family: 'Rokkitt', serif;
            font-weight: 400;

            position: absolute;
            bottom: 0%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .mainCourseContainer {
        position: relative;
        height: 30%;
        width: 100%;

        .mainCourse {
            height: 75%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .mainCourseTitle {
            color: $main-color;
            font-size: 1rem;
            font-family: 'Rokkitt', serif;
            font-weight: 400;

            position: absolute;
            bottom: 5%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .coctailContainer {
        position: relative;
        height: 30%;
        width: 100%;

        .coctail {
            height: 80%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .coctailTitle {
            color: $main-color;
            font-size: 1rem;
            font-family: 'Rokkitt', serif;
            font-weight: 400;

            position: absolute;
            bottom: -10%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }
}